	export default {
		data() {
			return {
				tempFace: ""
			};
		},
		onLoad(params) {
			var tempFilePath = params.tempFilePath;
			this.tempFace = tempFilePath;
		},
		methods: {
			// 重新选择头像
			changePendingFace() {
				var me = this;
				uni.chooseImage({
					count: 1,
					sizeType: ["compressed"],
					sourceType: ["album"],
					success(res) {
						// 获得临时路径
						var tempFilePath = res.tempFilePaths[0];
						me.tempFace = tempFilePath;
					}
				})
			},
			upload() {
				var me = this;
					var globalUser = uni.getStorageSync("globalUser");
				console.log(globalUser);
				uni.showLoading({
					mask: true,
					title: "上传中，请稍后"
				});
				
				uni.uploadFile({
					url: "http://10.22.52.250:8000/upload/"+globalUser[0].id,
					filePath: me.tempFace,
					name: "file",
					success(res) {
						// debugger;
						
						var resData = JSON.parse(res.data);
						console.log(resData);
						var userInfo = resData.data;
						console.log(userInfo);
						uni.setStorageSync("globalUser", userInfo);
						uni.navigateBack({
							delta: 1
						})
// 						var resDataStr = res.data;
// 						 console.log(typeof(resDataStr));
// 						var resData = JSON.parse(resDataStr);
// 						 console.log(typeof(resData));
// 							// 获得最新的用户数据
// 							var userInfo = resData.data;
// 							uni.setStorageSync("globalUser", userInfo);
// 							uni.navigateBack({
// 								delta: 1
// 							})
// 						} else if (resData.status == 502 || resData.status == 500) {
// 							uni.showToast({
// 								title: res.data.msg,
// 								image: "../../static/icos/error.png",
// 								duration: 2000
// 							})
// 						}
					},
					complete() {
						uni.hideLoading();
					}
				});
				
			}
		}
	}
